/* lazyload animation */
.lazyload, .lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}

@keyframes shake {
  2%, 18% {
    transform: translate3d(-1px, 0, 0);
  }
  4%, 16% {
    transform: translate3d(1px, 0, 0);
  }
  6%, 10%, 14% {
    transform: translate3d(-2px, 0, 0);
  }
  8%, 12% {
    transform: translate3d(2px, 0, 0);
  }
}
body {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  line-height: 24px;
}
body h1, body h2, body h3, body h4, body h5, body h6 {
  font-weight: bold;
}
body .page-title {
  font-size: 60px;
  margin-bottom: 15px;
}
@media (max-width: 768px) {
  body .page-title {
    font-size: 40px;
  }
}
body .page-subtitle {
  font-size: 30px;
}
@media (max-width: 768px) {
  body .page-subtitle {
    font-size: 20px;
  }
}

body, html {
  overflow-x: hidden;
}
body #main .background_controller, html #main .background_controller {
  position: absolute;
  width: 450px;
  max-width: 100%;
  height: auto;
  z-index: -1;
}
body #main .background_controller:first-of-type, html #main .background_controller:first-of-type {
  top: 1000px;
  left: 0;
}
body #main .background_controller:last-of-type, html #main .background_controller:last-of-type {
  top: 1500px;
  right: 0;
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1380px;
  }
}

header nav {
  background-color: transparent;
}
header nav .site_logo {
  max-width: 200px;
}
@media (max-width: 425px) {
  header nav .site_logo {
    max-width: 100px;
  }
}
header nav .menu-item {
  padding: 0 15px;
}
header nav .menu-item .nav-link {
  color: white;
}
header .topbar {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #ce3095;
  padding: 10px 0;
  transition: transform 0.3s;
  transform: translateY(-100%);
}
header .topbar.toggled {
  transform: translateY(0%);
}
header .topbar p {
  margin: 0;
}
header .topbar .button {
  background-color: #0db4b9;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 25px;
  padding: 10px 45px;
  transition: all 0.3s;
  display: inline-block;
  letter-spacing: 2px;
  font-size: 14px;
  border: 0;
  margin-left: 15px;
}
header .topbar .button:hover {
  cursor: pointer;
  text-decoration: none;
  filter: brightness(95%);
  transform: scale(0.95);
}
header .overlay {
  position: absolute;
  width: 100%;
  height: 1000px;
  background-image: linear-gradient(to right, #0db4b9, #1d1045);
  top: -54px;
  left: 0;
  z-index: -1;
  clip-path: polygon(0% 100%, 100% 44%, 100% 0%, 0% 0%);
}
header .overlay_2 {
  position: absolute;
  width: 100%;
  height: 1000px;
  background-image: linear-gradient(to right, #0db4b9, #1d1045);
  top: -40px;
  left: 0;
  z-index: -1;
  clip-path: polygon(0% 100%, 100% 44%, 100% 0%, 0% 0%);
  opacity: 0.5;
}
header .overlay, header .overlay_2 {
  transition: transform 1s;
  transform: translate(-100%, -100%) scale(0);
}
header .overlay.toggled, header .overlay_2.toggled {
  transform: translate(0%, 0%);
}
header .site-branding.landing-branding {
  margin: 0 auto;
}
@media (max-width: 425px) {
  header .site-branding.landing-branding {
    margin: 0;
  }
}

